@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  -ms-overflow-style: none;
}

body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  box-sizing: border-box;
}

.wrapper{display:inline-flex;margin-top:50px}.wrapper_Bottom{position:absolute;bottom:10px;z-index:110}.wrapper .icon{position:relative;background-color:#ffffff;color:#111;border-radius:50%;padding:15px;margin:10px;width:50px;height:50px;font-size:18px;display:flex;justify-content:center;align-items:center;flex-direction:column;box-shadow:0 10px 10px rgba(0,0,0,0.1);cursor:pointer;transition:all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper_Bottom .icon{width:30px;height:30px}a{color:inherit}.wrapper .tooltip{position:absolute;top:0;font-size:14px;padding:5px 8px;border-radius:5px;box-shadow:0 10px 10px rgba(0,0,0,0.1);opacity:0;pointer-events:none;width:150px !important;transition:all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper .tooltip::before{position:absolute;content:"";height:8px;width:8px;background-color:#ffffff;bottom:-3px;left:50%;transform:translate(-50%) rotate(45deg);transition:all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper .icon:hover .tooltip{top:-45px;opacity:1;visibility:visible;pointer-events:auto}.wrapper .icon:hover span,.wrapper .icon:hover .tooltip{text-shadow:0px -1px 0px rgba(0,0,0,0.1)}.wrapper .Itroduction:hover,.wrapper .Itroduction:hover .tooltip,.wrapper .Itroduction:hover .tooltip::before{background-color:#d35f32;color:#ffffff}.wrapper .Portfolio:hover,.wrapper .Portfolio:hover .tooltip,.wrapper .Portfolio:hover .tooltip::before{background-color:#2895c4;color:#ffffff}.wrapper .Contact:hover,.wrapper .Contact:hover .tooltip,.wrapper .Contact:hover .tooltip::before{background-color:#e1306c;color:#ffffff}*{margin:0;padding:0;box-sizing:border-box}*:focus,*:active{outline:none !important;-webkit-tap-highlight-color:transparent}img{-webkit-backface-visibility:hidden;backface-visibility:hidden}html{font-size:calc(10px + 1vmin)}.App{text-align:center}.App-header{background-color:#333f50;min-height:100vh;display:flex;flex-direction:column;align-items:center;align-content:center;justify-content:center;color:white;width:100%;overflow:hidden;position:relative}.divPhoto{position:relative;display:flex;flex-direction:column;align-items:center;justify-content:center}.divPhoto .ImgCircle{width:110%;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.IntroName{font-weight:bolder;font-size:1.5rem;margin-top:20px}.IntroTxt{margin-top:30px}

.divTag{margin:0 auto;width:100%;text-align:left;position:relative;z-index:90;display:flex;flex-direction:row;align-items:flex-end}.divTag .labTag{font-family:'Noto Sans TC', sans-serif;display:block;text-align:center;font-weight:bolder;background:#b9f1ff;color:#333F50;min-width:8rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding-top:7px;padding-bottom:5px;border-radius:20px 20px 0 0;margin-right:5px;font-size:1.0rem;font-weight:bolder;cursor:pointer;color:#6B6162}.divTag .labTag-selected{font-size:1.1rem;padding-top:12px;color:#fff;background:#5d6674;border-bottom:1px dotted #FED97E}.divIntroductionBG{position:absolute;width:100vw;height:100vh;top:0;left:0}.MainTxt{font-family:'Noto Sans TC', sans-serif;background:rgba(255,255,255,0.205);border-radius:0 25px 25px;margin:0 auto;width:70vw;text-align:right;padding:20px 50px;line-height:2rem;min-height:66vh;height:auto;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center;align-content:center;overflow-y:scroll;overflow:overlay;z-index:100;position:relative;color:#fff}.MainTxt::-webkit-scrollbar{width:5px}.MainTxt::-webkit-scrollbar-track{background:#888;margin:50px}.MainTxt::-webkit-scrollbar-thumb{background:#f1f1f1}.MainTxt::-webkit-scrollbar-thumb:hover{background:#555}.MainTxt-Top{align-items:flex-start !important;align-content:flex-start !important}.PicDeco{position:absolute;z-index:0}.PicDecoBG1{width:100vh;left:0px;top:0px;transform-origin:left top;transform:rotate(90deg) translateY(-60%)}.PicDeco1{left:0;top:15px;font-size:3.3rem;font-family:'Titillium Web', 'Noto Sans TC', sans-serif;font-weight:700;opacity:0.5;transform-origin:left top;line-height:3.4rem;transform:rotate(90deg) translateY(-100%);font-style:italic}.divIntroduction,.divSkill{width:100%;flex-shrink:0}.divThought{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-around;align-items:center;align-content:center;height:100%}.divThought h2{text-align:left;line-height:3rem}.divSkill{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;align-items:stretch;align-content:stretch;height:100%}.divSkill .SkillBlock{width:23%}.divSkill .SkillBlock .SkillTitle{font-size:2rem;line-height:2.2rem;font-weight:bold;text-align:center;padding:40px 0 18px 0;background:#8caac2;color:#fff}.divSkill .SkillBlock .SkillList span{text-align:center;display:block;margin:5px;font-weight:500}.divContact_MainItem{color:#FED97E;font-size:2.6rem;font-weight:bolder;position:relative}.divContact_item{position:relative;text-align:left;margin:15px}.divContact_item .Contact_val{margin-left:15px;display:inline-block}.divContact_item a{text-decoration:none}.imgBGPhoto{position:absolute;width:700px;max-width:40vw;opacity:.1;bottom:0;left:0;transform:rotate(-30deg) translate(-25%, 20%);filter:drop-shadow(rgba(255,255,255,0.8) 10px 5px)}.imgBGPhoto2{position:absolute;width:500px;opacity:.2;top:0;right:0;transform:rotate(40deg) translate(10%, -16%);filter:drop-shadow(rgba(255,255,255,0.8) -10px 5px)}.divFileMain{width:90%;height:80vmin;display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch;align-content:stretch}.divFileMain .divFileType{width:100%;height:70px;display:flex;justify-content:center;align-items:flex-start}.divFileMain .divFileType label{margin:0 40px;font-weight:bold;font-size:1.3rem;line-height:1rem;letter-spacing:1px;padding:10px 10px;border-radius:5px;color:#FED97E;background:transparent;cursor:pointer}.divFileMain .divFileType .labelSelect{transition:background-color 1.5s ease;color:#6B6162;background:#FED97E;border-bottom:1px solid #999}.divFileMain .divFileLists::-webkit-scrollbar{width:8px}.divFileMain .divFileLists::-webkit-scrollbar-track{background-color:#80797a;border-radius:100px}.divFileMain .divFileLists::-webkit-scrollbar-thumb{background-color:#b4aaab;border-radius:100px}.divFileMain .divFileLists{flex-grow:1;flex-shrink:1;display:flex;justify-content:space-around;align-items:flex-start;align-content:flex-start;flex-wrap:wrap;overflow-y:scroll;margin-top:20px}.divFileMain .divFileLists .divFileList{width:28%;text-align:left;cursor:pointer;margin:0px 0 50px 0}.divFileMain .divFileLists .divFileList img{width:100%}.divFileMain .divFileLists .divFileList h3{padding:0 5px;margin:5px 0;color:#FED97E;font-size:1.4rem}.divFileMain .divFileLists .divFileList .divFileSpecial{padding:0 5px;font-size:1rem}.divFileMain .divFileLists .divFileList .divFileSkill{padding:0 5px;font-size:0.9rem;margin-top:16px;color:#ffeecc;opacity:.8}.divFileMain .divFileLists .divFileList_NotLink{cursor:auto}

@media only screen and (max-width: 1600px){.MainTxt{width:70vw;height:70vh;text-align:left}}@media (max-width: 1200px) and (orientation: portrait){.MainTxt{height:78vh}}@media only screen and (max-width: 1200px){.divSkill{flex-wrap:wrap}.divSkill .SkillBlock{width:47%;margin:5px 0}.divSkill .SkillBlock .SkillTitle{font-size:2rem;font-weight:bolder;text-align:center;padding:40px 0 18px 0}.divSkill .SkillBlock .SkillList span{color:black;text-align:center;display:block;margin:5px;font-weight:500}}@media (max-width: 770px){.MainTxt{width:86vw;min-height:auto}.divSkill .SkillBlock{width:100%;margin:5px 0}.divSkill .SkillBlock:not(:first-child){margin-top:20px}.divSkill .SkillBlock .SkillTitle{font-size:2rem;padding:18px 0;margin-bottom:20px}.divSkill .SkillBlock .SkillList{display:grid;grid-template-columns:repeat(3, 1fr)}.divSkill .SkillBlock .SkillList span{margin:5px}.divThought{flex-wrap:wrap}.divThought h2{text-align:center}}@media (max-width: 600px){.divTag{width:92%;margin-top:-6vh}.divTag *{width:100%}.divTag .labTag{width:33.3%;margin:0}.divTag .MainTxt{align-items:flex-start;align-content:flex-start;border-radius:0 0 25px 25px;height:76vh;padding:20px}}

